import {SelectTopWidget} from "../../widget/slect_tokens/select_top";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../utils/formatBalance";
import {ButtonWidget} from "../../widget/base/button";
import React, {useCallback} from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {useStakeContext} from "../../provider/stake_provider";
import {TokenInterFace} from "../../web3/token.config";
import {useWeb3} from "../../provider/web3_provider";
import {useHistory} from "react-router-dom";
import {useDeposit, useUserInfo, useWithdraw} from "../../server/chef";
import {useBalance} from "../../utils/token_server";
import {StakeButtonWidget} from "./widget/button";
import {useSendModal} from "../../widget/modal/send_modal";
import BigNumber from "bignumber.js";

export const PreviewWidget = () => {

    const {totalDeposited, key, pid, tokenNick, contract} = useStakeContext();
    const {tokenMap} = useWeb3();
    const tokenItem: TokenInterFace = tokenMap[key];
    const history = useHistory();
    const {amount, deposits, rewardDebt} = useUserInfo(pid);
    const balance = useBalance(contract);
    const maxNumber = getBalanceNumber(balance);
    // 提交请求
    const [facethWithdraw] = useWithdraw();
    const sendWithdraw = useSendModal(facethWithdraw);
    const handleWithdraw = useCallback(async (data: string) => {
        let _number = getMaxNumber(data, amount);
        await sendWithdraw(pid, _number)
    }, [pid, amount],);


    // 提交请求
    const [facethDeposit] = useDeposit();

    const sendDeposit = useSendModal(facethDeposit);
    const handleDeposit = useCallback(async (amount: string) => {
        let _number = getMaxNumber(amount, new BigNumber(balance));
        await sendDeposit(pid, _number)
    }, [balance, pid]);

    return (
        <WapStyle>

            <MainStyle>
                <div className="wap">
                    <LogoWidget name={tokenNick} img={tokenItem.logo}/>
                    <CellWidget s1={'1196.5%'} s2={'Pool APY'}/>
                    <CellWidget s1={getBalanceFormat(totalDeposited)} s2={'Total Deposited'}/>
                    <CellWidget s1={getBalanceFormat(amount)} s2={'My Deposits'} img={true}/>
                    <CellWidget s1={`${getBalanceFormat(deposits)} SGT`} s2={'My Rewards'}/>
                    <div className="button">
                        <StakeButtonWidget
                            handleWithdraw={handleWithdraw}
                            handleDeposit={handleDeposit}
                        />
                    </div>
                </div>
            </MainStyle>
        </WapStyle>
    );
}


const CellWidget: React.FC<{ s1: string, s2: string, img?: boolean }> = ({s1, s2, img}) => {
    return (
        <CellStyle>
            <div>
                <h2>{s1}</h2>
                <h3>{s2}</h3>
            </div>

        </CellStyle>
    );
}
const LogoWidget: React.FC<{ name: string, img: string }> = ({name, img}) => {
    return (
        <CellStyle>
            <div>
                <h1>
                    <img src={img} alt=""/>
                    <span>{name}</span>
                </h1>
                <h3>Rewards Pool</h3>
            </div>
        </CellStyle>
    );
}

const WapStyle = styled.div`

`
